"use client";
import Link from "next/link";
import LanguageSwitch from "@/components/Header/LanguageSwitch";
import Logo from "@/components/Header/Logo";
import { Icon } from "@iconify/react";
import "./Header.css";

const navItems = [
  { label: "HOME", href: "/" },
  { label: "ABOUT US", href: "/about-philosophy" },
  { label: "OUR SERVICE", href: "/our-services" },
  { label: "PRICING", href: "/pricing" },
  { label: "CONTACT", href: "/contact-inquiry" },
];

const Header = () => {
  return (
    <header className="bjh-header">
      <div className="bjh-header-top">
        <div className="bjh-header-top-left">
          <span className="label">Call Center :</span>
          <a href="tel:020954799">02-095-4799</a>
          <span className="separator">/</span>
          <a href="tel:0864114262">086-411-4262</a>
          <span className="separator">|</span>
          <span className="label">Open daily</span>
          <span>11:00 AM - 8:00 PM</span>
        </div>
        <div className="bjh-header-top-right">
          <div className="header-social">
            <a
              href="https://www.facebook.com/bjhbangkok"
              target="_blank"
              rel="noreferrer"
              aria-label="Facebook">
              <Icon icon="ic:baseline-facebook" fontSize={20} />
            </a>
            <a
              href="https://www.instagram.com/bjh.bangkok"
              target="_blank"
              rel="noreferrer"
              aria-label="Instagram">
              <Icon icon="mdi:instagram" fontSize={20} />
            </a>
            <a
              href="https://www.tiktok.com/@bjhbangkok"
              target="_blank"
              rel="noreferrer"
              aria-label="TikTok">
              <Icon icon="mdi:tiktok" fontSize={20} />
            </a>
          </div>
          <LanguageSwitch className="lang-top" />
        </div>
      </div>

      <div className="bjh-header-main">
        <div className="logo-wrapper">
          <Logo className="logo-image" />
        </div>
        <nav className="bjh-nav-links">
          {navItems.map((item) => (
            <Link key={item.label} href={item.href} className="nav-link">
              {item.label}
            </Link>
          ))}
        </nav>
        <div className="header-ctas">
          <a href="tel:020954799" className="cta-pill">
            <span>Call Center</span>
            <strong>02-095-4799</strong>
          </a>
          <a
            href="https://lin.ee/D9KIJyb"
            target="_blank"
            rel="noreferrer"
            className="cta-pill outline">
            <strong>Line</strong>
          </a>
        </div>
      </div>
    </header>
  );
};

export default Header;
